import styled from "styled-components";

export const Button = styled.button`
  width: 100%;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
  user-select: none;
  font-size: 1rem;
  padding: 8px 16px;
  border-radius: 10px;
  white-space: nowrap;
`;